html {
  font-family: Circe, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.header {
  &__container {
    max-width: 1160px;
    padding: 16px 0;
    margin: 0 auto;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 4px;

    &-list {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-item {
      padding: 8px 20px;
      background: $clr_white;
      border-radius: 8px;
      transition: 0.3s;

      &:hover {
        background-color: $clr_background;
      }
    }

    &-link {
      font-weight: 700;
      line-height: 32px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__lang-menu {
    padding: 14px;
    border-radius: 8px;
    background: $clr_background;
  }

  &__contact-button {
    padding: 14px 24px;
    border-radius: 6px;
    background-color: $clr_accent;
    font-weight: 700;
    color: $clr_white;
  }
}